.card-wrapper {
  width: 100%;
  height: 528px;

  .card {
    position: relative;
    border-radius: 20px;
    overflow: fixed;
    width: 100%;
    justify-self: center;
    height: 528px;

    background-color: #fff;
    overflow: hidden;
    cursor: pointer;
    -webkit-box-shadow: 1px 26px 32px -3px rgba(204, 204, 204, 0.6);
    box-shadow: 1px 26px 32px -3px rgba(204, 204, 204, 0.6);
    &.open {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      border: none;
      background-color: white;
      cursor: default;
      z-index: 100;
      border-radius: 0;
      overflow-y: scroll;

      &.activeCard {
        background-color: aqua;
      }
    }

    .card-top {
      background-color: rgb(224, 185, 185);
      overflow: hidden;
    }

    .card-header .arrow:hover .arrow-text .arrow .live-site .card-top,
    .title,
    .description,
    .img {
      pointer-events: none;
    }

    .imgs img {
      border-radius: 10px;
    }
    .img {
      width: 100%;
      height: 450px;
      background-position: center;
      background-size: cover;
    }

    .title {
      position: absolute;
      bottom: 0;
      padding: 5px 20px;
      width: calc(100% - 40px);
      height: 40px;
      color: #fff;
      background-color: #222;
      font-size: 1.6rem;
    }
  }

  .description {
    padding: 20px;
  }
}

/* ARROW BUTTON */
.arrow {
  width: 50px;
  cursor: pointer;
  transition: margin-left 0.5s ease-out;
}

.card-header {
  margin: 0;
  padding: 10px;

  border-bottom: 1px solid #aaaa;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card-header h2 {
  font-size: larger;
  margin: 15px;
}

/* CANCEL BUTTON */
.cancel {
  height: 35px;
  width: 35px;
  padding-bottom: 7px;
  margin: 0;
  align-items: center;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  border-radius: 50px;
  background-color: rgb(245, 239, 239);
  border: 1px solid black;
  display: flex;

  position: absolute;
  margin: 20px;
  right: 0;
  top: 100;

  cursor: pointer;
  border: none;
  font-weight: 500;
  font-size: x-large;
}

.stack {
  font-size: small;
  border-radius: 5px;
  margin-bottom: 30px;
  width: fit-content;
  padding: 3px;
  margin-left: 10px;
  margin: 3px;
  background-color: burlywood;
}

.stack-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: start;
}
.stack-div {
  margin: 20px;
}

.imgs {
  display: flex;
  flex-direction: column;
  border: 1px solid #aaa;
  width: 100%;
  max-width: 400px;
  justify-items: center;
  margin: 20px;
  border-radius: 10px;
}

@media (min-width: 700px) {
  .imgs {
    display: flex;
    flex-direction: row;
    width: fit-content;
    justify-self: center;
  }

  .stack-wrapper {
    display: flex;
    grid-template-columns: 1fr;
    text-align: start;
  }
}
